    //FOOTER BUTTONS

    .icon-add {
      .filled {
        fill: #FFFFFF;
      }

      .stroked {
        stroke: #622262;
        stroke-width: 0.915;
        stroke-miterlimit: 10;
      }

      .filled-highlight {
        fill: #622262;
      }
    }

    .icon-single-product {
      .filled {
        fill: #C1D7DE;
      }

      .filled-dark {
        fill: #515151;
      }

      .filled-darker {
        fill: #2D2D2D;
      }

      .stroked {
        fill: none;
        stroke: #A1B3B7;
        stroke-miterlimit: 10;
        stroke-width: 1;
      }

      .stroked-highlight {
        stroke-width: 0.5;
      }
    }

    .catman-footer {
      .secondary-action {
        .btn-descriptors {
          grid-template-columns: auto auto 0.25rem;

          .label {
            font-family: "Sofia Pro", sans-serif;
          }

          .cm-icon {
            &:first-child {
              margin-right: 0.25rem;
            }
          }
        }
      }
    }

    .icon-edit {
      .filled-highlight {
        fill: #FFFFFF;
        stroke: #622262;
        stroke-width: 1;
        stroke-miterlimit: 10;
      }

      .no-fill {
        fill: none;
      }

      .filled {
        fill: $Core_80;
      }

      .stroked {
        fill: none;
        stroke: #622262;
        stroke-width: 0.6796;
        stroke-miterlimit: 10;
      }
    }

    .icon-cancel {
      &.ui-icon {
        height: 100%;
        width: 100%;
      }
    }

    .icon-delete {
      .filled {
        fill: $Core_80;
      }

      .filled-highlight {
        fill: white;
      }
    }

    .icon-compass {
      .filled-highlight {
        fill: $sky-blue;
      }

      .filled {
        fill: black;
      }

      .stroked {
        stroke: white;
      }

      .stroked-highlight {
        stroke: white;
      }
    }

    .icon-home, .icon-classes, .icon-attribute-mappings, .icon-catalog, .icon-attributes, .icon-attribute-groups {
      .filled {
        fill: $black_60;
      }

      .stroked {
        stroke: $black_60;
      }

      .stroked-highlight {
        stroke: white;
      }

      .filled-highlight {
        fill: white;
      }

      .no-fill {
        fill: none;
      }
    }

    .down-arrow-icon {
      .filled {
        fill: $black_50;
      }

      &.ui-icon {
        height: 0.5rem;
      }
    }


    //CHECKBOXES

  svg{
    pointer-events: none;
  }

    .cell {
      &.checkbox-main {
        background-color: white;
      }
    }

    svg.unchecked {
      .stroked {
        fill: #FFFFFF;
        stroke: #D8D8D8;
        stroke-width: 2;
        stroke-miterlimit: 10;
      }

      .filled {
        fill: none;
      }
    }

    svg.checked {
      .filled {
        fill: #05778C;
        stroke: white;
        stroke-width: 2;
        stroke-miterlimit: 10;
      }

      .filled-highlight {
        fill: white;
      }
    }





